<template>
  <!-- 登录页面的整体盒子 -->
  <div class="login-container">
    <!-- 登录的盒子 -->
    <div class="login-box">
      <!-- 标题的盒子 -->
      <div class="title-box">店铺后台管理系统</div>
      <!-- 登录的表单区域 -->
      <el-form :model="loginForm" :rules="loginRules" ref="loginForm">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            placeholder="请输入用户名"
            maxlength="10"
            minlength="1"
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            type="password"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="btn-login" @click="loginFn"
            >登录</el-button
          >
          <el-link type="info" @click="goRegiste">去注册</el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { loginAPI } from "@/api/index";
import { mapMutations } from "vuex";
export default {
  name: "my-login",
  data() {
    return {
      // 登录表单的数据对象
      loginForm: {
        username: "",
        password: "",
        flag: "b",
      },
      // 登录表单的验证规则对象
      loginRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            pattern: /^[a-zA-Z0-9]{1,10}$/,
            message: "用户名必须是1-10的字母数字",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            pattern: /^\S{6,15}$/,
            message: "密码必须是6-15的非空字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  //   =================================================以下是方法部分=================================================
  methods: {
    // 映射mutation的方法
    ...mapMutations(["updateToken"]),
    // 点击跳转至注册页面
    goRegiste() {
      this.$router.push("/reg");
    },
    loginFn() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          // 调用登录方法
          const { data: res } = await loginAPI(this.loginForm);
          // 提示错误信息
          if (res.code !== 0) return this.$message.error(res.message);
          // 提示登录成功的信息
          this.$message.success(res.message);
          // 将token存储到vuex中
          this.updateToken(res.token);
          // 跳转;
          this.$router.push("/");
        } else {
          this.$message.error("登录信息有误，请重新编辑。");
        }
      });
    },
  },

  //   =================================================以下是登录组件的部分=================================================
};
</script>

<style lang="less" scoped>
.login-container {
  background: url("../../assets/images/shop.jpg") center;
  background-size: cover;
  height: 100%;

  .login-box {
    width: 400px;
    height: 270px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 0 30px;
    box-sizing: border-box;

    .title-box {
      height: 60px;
      text-align: center;
      line-height: 60px;
      font-size: 30px;
      font-weight: 600;
      font-family: "楷体";
      color: rgb(102, 177, 255);
    }

    .btn-login {
      width: 100%;
    }
  }
}
</style>
